import './AdminProducto.css'

import { IonAlert } from '@ionic/react'
import { observer } from 'mobx-react-lite'
import React, { useRef, useState } from 'react'
import * as FIcon from 'react-feather'
import { RouteComponentProps } from 'react-router'

import { Page, SDataTable } from '../../components'
import { useToaster } from '../../helpers-ts'
import { useStores } from '../../models/root-store/root-store-context'
import { Producto } from '../../services/api'

const numeral = require('numeral')

const defaultValue = [
  {
    precio: 0,
    precio_mayorista: 0,
    id: 0,
  },
]

export const AdminPrecio: React.FC<RouteComponentProps> = observer(({ history }) => {
  const rootStore = useStores()
  const { loginStore } = rootStore
  const [ProductoSeleccionado, setProductoSeleccionado] = useState<Producto>()
  const DataTable = React.createRef<any>()
  const InputsRefs = useRef({})
  const Inputs2Refs = useRef({})
  const [Datos, setDatos] = useState(defaultValue)
  const ValorInicial = useRef({})
  const [Alert, setAlert] = useState(false)
  const [TextAlert, setTextAlert] = useState('')
  const [TitleAlert, setTitleAlert] = useState('')
  const addToast = useToaster()

  const guardar = (row: any) => {
    if (!row || !Datos[row.id]) {
      return
    }
    const precio_minorista = numeral(Datos[row.id].precio).value()
    const precio_mayorista = numeral(Datos[row.id].precio_mayorista).value()
    // @ts-ignore
    const precio_minorista_inicial = ValorInicial.current[row.id].precio
    const precio_minorista_inicial_f = numeral(precio_minorista_inicial).format('0,0')
    const precio_minorista_f = numeral(precio_minorista).format('0,0')
    // @ts-ignore
    const precio_mayorista_inicial = ValorInicial.current[row.id].precio_mayorista
    const precio_mayorista_inicial_f = numeral(precio_mayorista_inicial).format('0,0')
    const precio_mayorista_f = numeral(precio_mayorista).format('0,0')
    if (precio_minorista === precio_minorista_inicial && precio_mayorista === precio_mayorista_inicial) {
      addToast('Para guardar debe cambiar los valores', 'warning')
      return false
    }
    setAlert(true)
    setTitleAlert('Atención')
    if (precio_minorista !== precio_minorista_inicial) {
      setTextAlert(
        'Está seguro de que desea cambiar el costo minorista de ' +
          precio_minorista_inicial_f +
          ' a ' +
          precio_minorista_f +
          '?',
      )
    } else {
      setTextAlert(
        'Está seguro de que desea cambiar el costo mayorista de ' +
          precio_mayorista_inicial_f +
          ' a ' +
          precio_mayorista_f +
          '?',
      )
    }
    const datos = {
      id: Datos[row.id].id,
      precio: precio_minorista,
      precio_mayorista: precio_mayorista,
    }
    // @ts-ignore
    setProductoSeleccionado(datos)
  }

  const guardarDatos = () => {
    if (ProductoSeleccionado) {
      let datos = {}
      datos = {
        id: ProductoSeleccionado.id,
        precio: ProductoSeleccionado.precio,
        precio_mayorista: ProductoSeleccionado.precio_mayorista,
        tipo: 'precio',
      }
      rootStore.environment.api
        .guardarProducto(datos)
        .then((result: any) => {
          if (result.kind === 'ok') {
            addToast('Precio actualizado exitosamente', 'success')
          } else {
            if (result.kind === 'bad-data') {
              if (result.errors.non_field_errors) {
                addToast(result.errors.non_field_errors[0], 'warning')
              }
            } else {
              addToast('Ocurrió un error al guardar vuelva a intentar en unos momentos', 'error')
            }
          }
        })
    }
  }

  const renderAcciones = (row: any) => {
    return (
      <div>
        <span className={'button'} title={'Visualizar'} onClick={() => guardar(row)}>
          <FIcon.Save size={14} />
        </span>
      </div>
    )
  }

  const renderInputPrecioMinorista = (row: any) => {
    return (
      <div>
        <input
          ref={(ipt) => {
            // @ts-ignore
            InputsRefs.current[row.id] = ipt
          }}
          key={'id-' + row.id.toString()}
          type={'text'}
          className={'form-control'}
          // @ts-ignore
          value={numeral(Datos[row.id].precio).format('0,0')}
          onChange={(evt) => {
            const datos = { ...Datos }
            const res = { ...datos[row.id] }
            // @ts-ignore
            res.precio = evt.target.value
            datos[row.id] = res
            setDatos(datos)
            setTimeout(() => {
              // @ts-ignore
              InputsRefs.current[row.id].focus()
            }, 100)
          }}
        />
      </div>
    )
  }

  const renderInputPrecioMayorista = (row: any) => {
    return (
      <div>
        <input
          ref={(ipt) => {
            // @ts-ignore
            Inputs2Refs.current[row.id] = ipt
          }}
          type={'text'}
          className={'form-control'}
          // @ts-ignore
          value={numeral(Datos[row.id].precio_mayorista).format('0,0')}
          onChange={(evt) => {
            const datos = { ...Datos }
            const res = { ...datos[row.id] }
            // @ts-ignore
            res.precio_mayorista = evt.target.value
            datos[row.id] = res
            setDatos(datos)
            setTimeout(() => {
              // @ts-ignore
              Inputs2Refs.current[row.id].focus()
            }, 100)
          }}
        />
      </div>
    )
  }

  return (
    <Page login_required admin>
      <IonAlert
        isOpen={Alert}
        onDidDismiss={() => setAlert(false)}
        header={TitleAlert}
        message={TextAlert}
        buttons={[
          {
            text: 'Cancelar',
            role: 'cancel',
            cssClass: 'secondary',
            handler: () => {
              setAlert(false)
            },
          },
          {
            text: 'Confirmar',
            handler: () => guardarDatos(),
          },
        ]}
      />
      <div className="page-wrapper" style={{ display: 'flex', height: '100vh', overflowY: 'scroll' }}>
        <div className="container-fluid background-white">
          <div className="row page-titles">
            <div className="col-md-5 col-12 align-self-center">
              <h3 className="text-themecolor">Precios</h3>
            </div>
            <div className="col-md-7 col-12 align-self-center d-none d-md-block">
              <ol className="breadcrumb mb-0 p-0 bg-transparent float-right">
                <li className="breadcrumb-item">
                  <a onClick={() => history.push('/admin/')}>Inicio</a>
                </li>
                <li className="breadcrumb-item active">Precios</li>
              </ol>
            </div>
          </div>
          <div className="card mb-0">
            <div className="card-body">
              <SDataTable
                ref={DataTable}
                subHeader
                obtenerDatos={async (perPage: number, page: number, order: any, search: string) => {
                  const result = await rootStore.environment.api.productos(page, perPage, order, search, true)
                  if (result.kind === 'ok') {
                    const datos = {}
                    // @ts-ignore
                    result.data.results.map((res: any) => {
                      // @ts-ignore
                      datos[res.id] = res
                    })
                    // @ts-ignore
                    setDatos(datos)
                    // @ts-ignore
                    ValorInicial.current = datos
                  }
                  return result
                }}
                columns={[
                  {
                    name: 'Fabricante',
                    selector: 'fabricante',
                    sortable: true,
                    omit: !loginStore.administrador,
                    cell: (row: any) => row.fabricante.nombre,
                  },
                  {
                    name: 'Producto',
                    selector: 'nombre',
                    sortable: true,
                  },
                  {
                    name: 'Costo minorista',
                    selector: 'precio',
                    sortable: true,
                    cell: (row: any) => renderInputPrecioMinorista(row),
                  },
                  {
                    name: 'Costo Mayorista',
                    selector: 'precio_mayorista',
                    sortable: true,
                    cell: (row: any) => renderInputPrecioMayorista(row),
                  },
                  {
                    name: 'Acciones',
                    sortable: false,
                    cell: (row: any) => renderAcciones(row),
                  },
                ]}
              />
            </div>
          </div>
        </div>
      </div>
    </Page>
  )
})

export default AdminPrecio
